 <script>  
  import Tab from './tab'
  import Loading from 'partials/Loading'

  export default {
    name: 'tabs',

    data () {
      return {
        selected: null
      }
    },

    props: {
      tabs: {
        type: Array,
        required: true
      },
      category_id: {
        type: Number,
        required: true
      }
    },

    components: {
      Tab
    },

    methods: {
      isSelected (tab) {
        const selected = !tab.id ? true : this.selected === tab.id

        return selected
      },
      add () {
        this.$emit('addTab')
      },
      del (tab) {
        this.$emit('delTab', tab)
      },
      update (tabs) {
        this.$emit('update', tabs)
      },
      updateImages (tab) {
        this.$emit('updateImages', tab)
      },
      selectTab (id) {
        this.selected = id
      }
    },

    computed: {
      data () {
        return this.tabs
      }
    }
  }
</script>

<template>
	<div class="row row-reverse">
    <div class="col-12 mb-3 mt-3 text-center">
      <div class="btn btn-primary btn-with-icon btn-micro" @click="add">
        {{ $t('category.tab.add') }}
      </div>
    </div>

    <tab
      v-for="(tab, index) in this.data" 
      :data="tab" 
      @del="del"
      :key="index + 1"
      :selected="isSelected(tab)"
      :category_id="category_id"
      @update="update"
      @selectTab="selectTab"
      @updateImages="updateImages"
    />
	</div>
</template>
