<script>  
  import api from 'src/packages/Api'

  import Modal from 'view/components/modal'
  import Editor from 'view/components/editor'
  import Uploader from 'view/components/uploader'

  export default {
    name: 'tab',

    data () {
      return {
        tab: {},
        id: '',
        title: '',
        sort: 1,
        key: '',
        editor: null,

        loaded: false,
        images: false
      }
    },

    props: {
	    data: {
        type: Object,
        required: true
      },
      selected: {
        type: Boolean,
        default: false
      },
      category_id: {
        type: Number,
        required: true
      }
	  },

    components: {
      Modal,
      Editor,
      Uploader
    },

    created () {
      this.init()
    },

    methods: {
      init () {
        if (this.data.id) {
          const self = this

          this.tab = this.data
          this.id = this.tab.id
          this.title = this.tab.title
          this.sort = this.tab.sort
          this.key = this.tab.key
          setTimeout(() => self.images = true, 1000)
        }
      },
    	add () {
        const self = this

        if (this.tab.id) {
          const data = {
            key: this.key,
            sort: this.sort,            
            title: this.title,            
            content: this.editor.getContents()
          }

          api.put('categories/tabs/' + this.category_id + '/' + this.tab.id, data).then(response => {
            this.$store.dispatch('addToastMessage',
              { text: this.$t('category.tab.updated'), type: 'success' }
            )

            this.$emit('update', response.data)
            this.$emit('selectTab', null)
          }).catch(error => {
            for (const e in error.data.errors) {
              error.data.errors[e].map(text => {
                this.$store.dispatch('addToastMessage', { text } )
              })
            }
          })
        } else {
          const tab = {
            category_id: this.category_id,
            sort: this.sort,
            key: this.key,
            title: this.title,
            content: this.editor.getContents()
          }
          
          api.post('categories/tabs/' + this.category_id, tab).then(response => {
            this.$store.dispatch('addToastMessage',
              { text: this.$t('category.tab.created'), type: 'success' }
            )
            
            this.$emit('update', response.data)
            // this.$emit('selectTab', null)
          }).catch(error => {
            for (const e in error.data.errors) {
              error.data.errors[e].map(text => {
                this.$store.dispatch('addToastMessage', { text } )
              })
            }
          })
        }
      },
      del () {
        const self = this 

        this.$swal({
          title: 'Are you sure?',
          text: "You won't be able to revert this!",
          type: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          confirmButtonText: 'Yes, delete it!'
        }).then((result) => {
          if (result.value) {            
            if (self.tab.id) {
              api.delete('categories/tabs/' + self.category_id + '/' + self.tab.id).then(response => {
                this.$store.dispatch('addToastMessage',
                  { text: this.$t('category.tab.updated'), type: 'success' }
                )

                this.$emit('update', response.data)
              }).catch(error => {
                this.$store.dispatch('addToastMessage',
                  { text: this.$t('errors.any'), error }
                )
              })
            } else {
              self.$emit('del', self.data)
            }
          }
        })
      },
      updateTab () {
        this.$emit('selectTab', this.tab.id)
      },
      closeTab () {
        this.$emit('selectTab', null)
      },
      handleEditor (editor) {
        this.editor = editor

        if (this.tab.id) {
          this.editor.setContents(this.tab.content || '')
        }
      },
      
      getImages () {
        const images = this.tab.images.data || []

        return images
      },

      getImage () {
        const image = this.tab.image
          ? [this.tab.image.data] 
          : []

        return image
      },

      getHover () {
        const cover = this.tab.hover
          ? [this.tab.hover.data] 
          : []

        return cover
      },

      reInitImages (response) {
        this.images = false

        this.$emit('updateImages', response.data)
      }
    },
    computed: {
      apiUrl () {
        return 'categories/tabs/' + this.tab.id + "/upload/images"
      },
      hoverApiUrl () {
        return 'categories/tabs/' + this.tab.id + "/upload/hover"
      },
      imageApiUrl () {
        return 'categories/tabs/' + this.tab.id + "/upload/image"
      }
    },
    watch: {
      data () {
        this.init()
      }
    }
  }
</script>

<template>
  <div class="col-2">
    <div class="row text-center" v-if="tab.id" >
      <div class="col">
        <a @click="updateTab"  style="color: #49e387;">{{ tab.title }}</a>
      </div>
    </div>

    <modal :big="true" v-if="selected">
      <template slot="header" v-if="tab.id">
        <div class="col text-right">
          {{ tab.title }}
        </div>
      </template>

      <template slot="body">
        <div class="col">
          <div class="row row-reverse">
            <div class="col-6">
              <div class="form-group with-icon-right"
                   :class="{'has-error': errors.has('title')}">
                <div class="input-group">
                  <input
                    id="title"
                    name="title"
                    v-model="title"
                  />
                  <i
                    class="fa fa-exclamation-triangle icon-right input-icon"
                    v-show="errors.has('title')"></i>
                  <label class="control-label" for="slug">{{'category.tab.title'
                    | translate}}</label><i class="bar"></i>
                  <small v-show="errors.has('title')"
                         class="help text-danger">{{
                    errors.first('title')
                    }}
                  </small>
                </div>
              </div>
            </div>

            <div class="col-3">
              <div class="form-group with-icon-right"
                   :class="{'has-error': errors.has('sort')}">
                <div class="input-group">
                  <input
                    id="sort"
                    name="sort"
                    v-model="sort"
                    type="number"
                  />
                  <i
                    class="fa fa-exclamation-triangle icon-right input-icon"
                    v-show="errors.has('sort')"></i>
                  <label class="control-label" for="sort">{{'category.tab.sort'
                    | translate}}</label><i class="bar"></i>
                  <small v-show="errors.has('sort')"
                         class="help text-danger">{{
                    errors.first('sort')
                    }}
                  </small>
                </div>
              </div>
            </div>

            <div class="col-3">
              <div class="form-group with-icon-right"
                   :class="{'has-error': errors.has('key')}">
                <div class="input-group">
                  <input
                    id="key"
                    name="key"
                    v-model="key"
                  />
                  <i
                    class="fa fa-exclamation-triangle icon-right input-icon"
                    v-show="errors.has('key')"></i>
                  <label class="control-label" for="key">{{'category.tab.key'
                    | translate}}</label><i class="bar"></i>
                  <small v-show="errors.has('key')" class="help text-danger">
                    {{
                      errors.first('key')
                    }}
                  </small>
                </div>
              </div>
            </div>
          </div>

          <div class="row mt-2" v-if="tab.id && images">
            <div class="col-3">
              <Uploader
                :maxFiles="1"
                :placeholder="'category.tab.hover' | translate"
                :images="getHover"
                :apiUrl="hoverApiUrl"
                @onChange="reInitImages"
              />
            </div>
            <div class="col-3">
              <Uploader
                :maxFiles="1"
                :placeholder="'category.tab.image' | translate"
                :images="getImage"
                :apiUrl="imageApiUrl"
                @onChange="reInitImages"
              />
            </div>
            <div class="col-6">
              <Uploader
                :placeholder="'category.tab.images' | translate"
                :images="getImages"
                :apiUrl="apiUrl"
                @onChange="reInitImages"
              />
            </div>
          </div>

          <div class="row mt-2">
            <Editor @editor="handleEditor" :editor_id="`tab_${data.id}`" :mode="'classic'"/>
          </div>
        </div>
      </template>

      <template slot="footer">
        <div class="btn btn-danger btn-with-icon btn-micro" @click="del">
          <div class="btn-with-icon-content">
            {{'category.tab.delete' | translate}}
          </div>
        </div>

        <div class="btn btn-warning btn-with-icon btn-micro" @click="closeTab" v-if="tab.id">
          <div class="btn-with-icon-content">
            {{'modal.close' | translate}}
          </div>
        </div>

        <div class="btn btn-primary btn-with-icon btn-micro" @click="add">
          <div class="btn-with-icon-content">
            {{ tab.id ? 'category.tab.update' : 'category.tab.add' | translate}}
          </div>
        </div>
      </template>    
    </modal>
  </div>  
</template>

<style>
  .btn.btn-with-icon .btn-with-icon-content {
    margin-left: 0!important;
  }

  .modal-body .row div:first-child,
  .modal-body .row div:first-child {
    color: black;
  }
</style>